import {Const} from "../../utils/Const";
import showModal from "@/components/showModal.vue";

export default {
    components: {showModal},

    data() {
        return {
            servicePhone: Const.servicePhone
        }
    },

    methods: {
        // 拨打电话
        takePhone() {
            this.$refs.modal.show();
        },

        onModalChanged(val) {
            if (val) {
                this.DifferenceApi.makePhoneCall(this.servicePhone);
            }
        }
    }
}
